.navbar .navbar-title {
  color: #414b56;
  font-size: 24px; }

.navbar-li {
  color: #414b56;
  font-size: 18px; }

.navbar {
  grid-area: navbar;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  padding-left: 100px;
  height: 50%;
  margin-top: 50px;
