.add-demand {
  color: #414b56;
  font-size: 18px; }

.dashboard {
  width: 75%;
  margin: 50px auto; }

.dashboard-table {
  width: 100%;
  margin-right: 25px; }

.dashboard-table-title {
  margin-bottom: 10px; }

.add-demand {
  margin: 20px 15px;
  padding: 5px 10px;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }
  .add-demand:hover {
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    cursor: pointer;
    color: #4FBEFF; }



